.ContentContainer {
    width: calc(100% - 300px);
    float: right;
    box-sizing: border-box;
}

@media (max-width: 500px) {
    .ContentContainer{
        width: 100%;
    }
    
    .Backdrop {
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: 5;
        left: 0;
        top: 0;
        background-color: rgba(0, 0, 0, 0.5);
    }
}